.forecast-card-container {
  height: 80%;
  width: 80%;
  display: grid;
  grid-auto-flow: row dense;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 0px 18px;
  align-items: center;
  justify-items: center;
  justify-content: center;
}
